@media screen and (max-width: 425px) {
  body {
    font-size: 12px;
  }

  .title {
    font-size: 2em;
  }

  .container {
    margin: 0 3vw;
  }

  .block-list {
  
    .container {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
    }
  }

  .preview {
    &__screen {
      height: 50vh;
  
      &-big {
        height: 75vh;
      }
      
      &-shmol {
        height: 35vh;
      }
    }
  }

  .footer {
    color: #ffffff;
  
    &__first {
      padding: 4em 0;
      background-color: #3D3D3D;
  
      .container {
        display: flex;
        flex-direction: column;
        gap: 3em;
        align-items: center;
        justify-content: space-between;
      }
    }
  }

  .block {
    &-hiking {
  
      &__routes {
        &-gallary {
  
          &-item {
            font-size: 1.4em;
  
            &-primary {
              width: 100%;
  
              span {
                bottom: 2em;
                left: 2em;
              }
            }
  
            &-secondary {
              width: 100%;
  
              span {
                bottom: 2em;
                left: 2em;
              }
            }
          }
        }
      }
    }

    &-jeeping {
  
      &__description {
        &-content {
          &-blue {
            
            .subtitle {
              font-size: 1.4em;
            }
          }
        }
      }
    }
  }
}